import React, { useEffect, useRef, Component } from 'react';
import './App.css';
import ChatInterface from './components/ChatInterface';
import BuildingModel from './components/BuildingModel';
import SlideSelector from './components/SlideSelector';
import ParameterControls from './components/ParameterControls';
import LeftPanel from './components/LeftPanel';
import useStore from './store';

// 错误边界组件
class ErrorBoundary extends Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false, error: null, errorInfo: null };
  }

  static getDerivedStateFromError(error) {
    return { hasError: true, error };
  }

  componentDidCatch(error, errorInfo) {
    console.error('应用错误:', error, errorInfo);
    this.setState({
      error: error,
      errorInfo: errorInfo
    });
  }

  render() {
    if (this.state.hasError) {
      return (
        <div className="error-boundary">
          <h2>应用出现错误</h2>
          <p>{this.state.error && this.state.error.toString()}</p>
          <button onClick={() => window.location.reload()}>刷新页面</button>
        </div>
      );
    }

    return this.props.children;
  }
}

function App() {
  const { buildingData, isLoading, error, initializeApp, updateAvailableSlides } = useStore();
  const modelContainerRef = useRef(null);

  useEffect(() => {
    console.log('App 组件挂载，开始初始化...');
    initializeApp();
    
    // 初始化 availableSlides
    updateAvailableSlides([
      {
        key: 'roofType',
        title: '屋顶类型',
        options: [
          { value: 'gabled', label: '山形屋顶' },
          { value: 'flat', label: '平屋顶' },
          { value: 'pitched', label: '斜屋顶' }
        ]
      },
      {
        key: 'wallColor',
        title: '墙体颜色',
        options: [
          { value: '#f5f5f5', label: '白色' },
          { value: '#cccccc', label: '灰色' },
          { value: '#8b4513', label: '棕色' }
        ]
      },
      {
        key: 'windowColor',
        title: '窗户颜色',
        options: [
          { value: '#88ccff', label: '浅蓝色' },
          { value: '#ffffff', label: '白色' },
          { value: '#cccccc', label: '灰色' }
        ]
      }
    ]);
  }, [initializeApp, updateAvailableSlides]);

  // 显示加载状态
  if (isLoading) {
    return <div className="loading">Loading...</div>;
  }

  // 显示错误信息
  if (error) {
    return (
      <div className="error">
        <h2>加载错误</h2>
        <p>{error}</p>
        <button onClick={initializeApp}>重试</button>
      </div>
    );
  }

  // 检查建筑数据是否存在
  if (!buildingData || !buildingData.building) {
    return (
      <div className="error">
        <h2>数据错误</h2>
        <p>建筑数据无效或为空</p>
        <button onClick={initializeApp}>重新加载</button>
      </div>
    );
  }

  return (
    <ErrorBoundary>
    <div className="app-container">
      <LeftPanel />
      <div className="model-section">
        <div id="building-model" className="model-container" ref={modelContainerRef}>
            <BuildingModel buildingData={buildingData} />
        </div>
      </div>
      <div className="panel-section">
        <div className="chat-section">
          <ChatInterface />
        </div>
        <div className="selector-section">
          <ParameterControls />
          <div className="slide-selector">
            <SlideSelector />
          </div>
        </div>
      </div>
    </div>
    </ErrorBoundary>
  );
}

export default App; 