// 默认选中值
const defaultSelectedValues = {
  roofType: 'gabled',
  wallColor: '#f5f5f5',
  windowColor: '#88ccff'
};

const useStore = create((set, get) => ({
  // 建筑数据
  buildingData: null,
  isLoading: true,
  error: null,

  // 聊天消息
  messages: [],
  isLoadingChat: false,

  // 滑块选择器
  availableSlides: defaultSlides,
  selectedValues: defaultSelectedValues,

  // 渐进式参数调整相关状态
  currentParameterIndex: 0,
  parameterAdjustmentComplete: false,
  nextParameter: null,
  parameterHistory: [],

  // 初始化应用
  initializeApp: async () => {
    set({ isLoading: true, error: null });
    try {
      console.log('开始初始化应用...');
      // 尝试从本地文件加载数据
      console.log('尝试从 /hs.json 加载数据...');
      const response = await fetch('/hs.json');
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const data = await response.json();
      console.log('成功加载数据:', data);
      
      // 检查数据结构
      if (!data || typeof data !== 'object') {
        console.error('加载的数据不是有效的对象');
        set({ buildingData: defaultBuildingData, error: '加载的数据不是有效的对象', isLoading: false });
        return;
      }
      
      if (!data.building) {
        console.error('数据缺少 building 属性');
        set({ buildingData: defaultBuildingData, error: '数据缺少 building 属性', isLoading: false });
        return;
      }
      
      // 验证数据
      console.log('开始验证数据...');
      const validationResult = validateBuildingData(data);
      console.log('验证结果:', validationResult);
      
      if (!validationResult.valid) {
        console.error('无效的建筑数据:', validationResult.error);
        console.log('使用默认建筑数据作为回退...');
        set({ buildingData: defaultBuildingData, error: validationResult.error, isLoading: false });
        return;
      }
      
      console.log('数据验证通过，更新状态...');
      set({ buildingData: data, error: null, isLoading: false });
    } catch (error) {
      console.error('加载建筑数据时出错:', error);
      console.log('使用默认建筑数据作为回退...');
      set({ buildingData: defaultBuildingData, error: error.message, isLoading: false });
    }
  },

  // 获取初始建筑数据
  fetchInitialBuildingData: async () => {
    set({ isLoading: true, error: null });
    try {
      // 尝试从服务器获取数据
      const response = await fetch('/api/building-data');
      
      // 检查响应类型
      const contentType = response.headers.get('content-type');
      if (contentType && contentType.includes('application/json')) {
        // 如果是 JSON，则解析
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const data = await response.json();
        set({ buildingData: data, isLoading: false });
      } else {
        // 如果不是 JSON，尝试从本地文件加载
        console.warn('Server did not return JSON, trying to load from local file');
        try {
          const localResponse = await fetch('/hs.json');
          if (!localResponse.ok) {
            throw new Error(`HTTP error! status: ${localResponse.status}`);
          }
          const localData = await localResponse.json();
          set({ buildingData: localData, isLoading: false });
        } catch (localError) {
          console.warn('Failed to load local JSON, using default building data');
          set({ buildingData: defaultBuildingData, isLoading: false });
        }
      }
    } catch (error) {
      console.error('Error fetching building data:', error);
      // 出错时使用默认数据
      set({ buildingData: defaultBuildingData, error: error.message, isLoading: false });
    }
  },

  // 更新建筑数据
  updateBuildingData: (newData) => {
    // 验证新的建筑数据
    const validationResult = validateBuildingData(newData);
    if (!validationResult.valid) {
      console.error('Invalid building data:', validationResult.error);
      set({ error: validationResult.error });
      return false;
    }
    
    // 确保屋顶属性在正确的位置
    if (newData.building && newData.building.floors && newData.building.floors.length > 0) {
      // 找到最高层
      const highestFloor = newData.building.floors.reduce((highest, floor) => {
        return (floor.level || 0) > (highest.level || 0) ? floor : highest;
      }, newData.building.floors[0]);
      
      // 如果建筑有屋顶属性，移动到最高层
      if (newData.building.roof) {
        highestFloor.roof = newData.building.roof;
      }
    }
    
    set({ buildingData: newData, error: null });
    return true;
  },

  // 发送聊天消息
  sendMessage: async (message) => {
    set((state) => ({
      messages: [...state.messages, { role: 'user', content: message }],
      isLoadingChat: true,
    }));

    try {
      // 尝试发送消息到服务器
      const response = await fetch('/api/chat', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message,
          buildingData: useStore.getState().buildingData,
        }),
      });

      // 检查响应类型
      const contentType = response.headers.get('content-type');
      if (contentType && contentType.includes('application/json')) {
        // 如果是 JSON，则解析
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const data = await response.json();
        
        set((state) => ({
          messages: [...state.messages, { role: 'assistant', content: data.response }],
          buildingData: data.buildingData || state.buildingData,
          isLoadingChat: false,
        }));
      } else {
        // 如果不是 JSON，使用默认响应
        console.warn('Server did not return JSON, using default response');
        set((state) => ({
          messages: [...state.messages, { role: 'assistant', content: '抱歉，服务器暂时无法处理您的请求。' }],
          isLoadingChat: false,
        }));
      }
    } catch (error) {
      console.error('Error sending message:', error);
      set((state) => ({
        messages: [...state.messages, { role: 'assistant', content: '抱歉，处理您的请求时遇到了错误。' }],
        isLoadingChat: false,
      }));
    }
  },

  // 更新可用的滑块
  updateAvailableSlides: (slides) => {
    set({ availableSlides: slides });
  },

  // 更新选中的值
  updateSelectedValue: (slideKey, value) => {
    set((state) => ({
      selectedValues: {
        ...state.selectedValues,
        [slideKey]: value,
      },
    }));
  },
  
  // 处理滑块选择并发送到 GPT
  handleSlideSelection: async (slideKey, value) => {
    // 更新选中的值
    set((state) => ({
      selectedValues: {
        ...state.selectedValues,
        [slideKey]: value,
      },
    }));
    
    // 构建用户消息
    let userMessage = '';
    const slide = get().availableSlides.find(s => s.key === slideKey);
    const option = slide.options.find(o => o.value === value);
    
    if (slide && option) {
      userMessage = `将${slide.title}改为${option.label}`;
    } else {
      userMessage = `修改${slideKey}为${value}`;
    }
    
    // 添加到消息列表
    set((state) => ({
      messages: [...state.messages, { role: 'user', content: userMessage }],
      isLoadingChat: true,
    }));
    
    try {
      // 获取当前建筑数据
      const currentBuildingData = get().buildingData;
      
      // 发送到 GPT
      const result = await sendToGPT(userMessage, currentBuildingData);
      
      if (result.success) {
        // 检查是否是参数推荐数据
        if (result.isParameterRecommendation) {
          // 如果是参数推荐数据，直接更新状态
          set((state) => ({
            messages: [...state.messages, { role: 'assistant', content: result.message }],
            isLoadingChat: false,
            error: null
          }));
          return;
        }
        
        // 验证并更新建筑数据
        const validationResult = validateBuildingData(result.data);
        if (validationResult.valid) {
          set((state) => ({
            buildingData: result.data,
            messages: [...state.messages, { role: 'assistant', content: result.message }],
            isLoadingChat: false,
            error: null
          }));
        } else {
          // 数据验证失败
          set((state) => ({
            messages: [...state.messages, { role: 'assistant', content: `错误：${validationResult.error}` }],
            isLoadingChat: false,
            error: validationResult.error
          }));
        }
      } else {
        // GPT 处理失败
        set((state) => ({
          messages: [...state.messages, { role: 'assistant', content: `错误：${result.error}` }],
          isLoadingChat: false,
          error: result.error
        }));
      }
    } catch (error) {
      console.error('Error processing slide selection:', error);
      set((state) => ({
        messages: [...state.messages, { role: 'assistant', content: '抱歉，处理您的请求时遇到了错误。' }],
        isLoadingChat: false,
        error: error.message
      }));
    }
  },
  
  // 直接发送消息到 GPT
  sendToGPT: async (message) => {
    // 添加到消息列表
    set((state) => ({
      messages: [...state.messages, { role: 'user', content: message }],
      isLoadingChat: true,
    }));
    
    try {
      // 获取当前建筑数据
      const currentBuildingData = get().buildingData;
      
      // 发送到 GPT
      const result = await sendToGPT(message, currentBuildingData);
      
      if (result.success) {
        // 检查是否是参数推荐数据
        if (result.isParameterRecommendation) {
          // 如果是参数推荐数据，直接更新状态
          set((state) => ({
            messages: [...state.messages, { role: 'assistant', content: result.message }],
            isLoadingChat: false,
            error: null
          }));
          return;
        }
        
        // 验证并更新建筑数据
        const validationResult = validateBuildingData(result.data);
        if (validationResult.valid) {
          set((state) => ({
            buildingData: result.data,
            messages: [...state.messages, { role: 'assistant', content: result.message }],
            isLoadingChat: false,
            error: null
          }));
        } else {
          // 数据验证失败
          set((state) => ({
            messages: [...state.messages, { role: 'assistant', content: `错误：${validationResult.error}` }],
            isLoadingChat: false,
            error: validationResult.error
          }));
        }
      } else {
        // GPT 处理失败
        set((state) => ({
          messages: [...state.messages, { role: 'assistant', content: `错误：${result.error}` }],
          isLoadingChat: false,
          error: result.error
        }));
      }
    } catch (error) {
      console.error('Error sending message to GPT:', error);
      set((state) => ({
        messages: [...state.messages, { role: 'assistant', content: '抱歉，处理您的请求时遇到了错误。' }],
        isLoadingChat: false,
        error: error.message
      }));
    }
  },

  // 设置下一个参数
  setNextParameter: async (isFirstCall = false) => {
    try {
      // 获取当前建筑数据
      const currentBuildingData = get().buildingData;
      
      // 如果是首次调用，添加随机初始参数选择逻辑
      if (isFirstCall) {
        console.log('首次调用setNextParameter，随机选择初始参数...');
        
        // 初始参数候选列表 - 这些是建筑基础参数，适合作为起点
        const initialParameters = [
          {
            key: 'height',
            label: '建筑高度',
            min: 2500,
            max: 4000,
            value: 3000,
            step: 100,
            description: '建筑高度决定了整体空间感和比例。一般住宅建筑层高在2700-3500毫米较为舒适，既提供良好的空间感又不浪费能源。较高的层高带来更好的通风和采光，但也会增加建造和供暖成本。'
          },
          {
            key: 'floorCount',
            label: '楼层数量',
            min: 1,
            max: 4,
            value: 1,
            step: 1,
            description: '楼层数量直接影响建筑的使用面积和垂直空间分布。单层建筑适合老人和行动不便者，而多层建筑可在有限占地上提供更多空间。考虑到建筑性能和消防安全，普通住宅通常在1-3层为宜。'
          },
          {
            key: 'wallThickness',
            label: '墙体厚度',
            min: 100,
            max: 400,
            value: 200,
            step: 10,
            description: '墙体厚度影响建筑的保温性能、结构稳定性和隔音效果。一般外墙厚度在200-300毫米较为合理，能提供良好的保温隔热；内墙可选择100-150毫米，满足承重和隔音需求。较厚的墙体有利于建筑的长期稳定性。'
          },
          {
            key: 'roofHeight',
            label: '屋顶高度',
            min: 500,
            max: 2500,
            value: 1500,
            step: 100,
            description: '屋顶高度调整可以改变建筑的轮廓和空间感。较高的屋顶能提供更好的排水和保温性能，也能增加阁楼空间。建议根据整体建筑高度选择合适的屋顶高度，通常为总高度的1/4到1/3，以保持建筑的和谐比例。'
          },
          {
            key: 'roofPitch',
            label: '屋顶坡度',
            min: 0,
            max: 60,
            value: 30,
            step: 5,
            description: '屋顶坡度决定了屋顶的排水效果和风格特点。较陡的坡度(30-45度)适合雨雪较多的地区，能有效排水防积雪；平缓的坡度(15-25度)则更适合干燥气候，并能带来现代简约感。坡度选择还应考虑周边建筑风格以保持环境和谐。'
          },
          {
            key: 'totalArea',
            label: '总建筑面积',
            min: 50,
            max: 300,
            value: 100,
            step: 10,
            description: '建筑面积是功能分区的基础参数。现代舒适的单人住宅通常需要60-80平方米，家庭住宅则需要100-150平方米以上。增加面积可提供更宽敞的生活空间，但也会增加建造和维护成本。应根据居住人数、生活方式和经济条件选择合适面积。'
          }
        ];
        
        // 随机选择一个参数 (或者可以设置为数组中的第一个)
        const randomIndex = Math.floor(Math.random() * initialParameters.length);
        const initialParam = initialParameters[randomIndex];
        
        console.log(`随机选择初始参数: ${initialParam.key}`);
        
        // 更新状态
        set((state) => ({ 
          nextParameter: initialParam,
          parameterAdjustmentComplete: false,
          currentParameterIndex: state.currentParameterIndex
        }));
        
        return;
      }
      
      // 对于非首次调用，使用原有逻辑向GPT请求参数推荐
      // 构建更详细的提示消息
      const prompt = `请根据当前建筑状态推荐下一个需要调整的参数，并用自然友好的语气描述该参数。
当前已调整的参数: ${get().parameterHistory.join(', ')}

请确保按照以下规则推荐参数：
1. 避免重复推荐已调整的参数
2. 返回的JSON格式必须包含key, label, min, max, value等字段
3. 参数命名遵循驼峰命名法，如roofHeight, wallThickness
4. 不要在key中重复包含元素类型，例如使用"roofPitch"而不是"pitch_roof"

对于颜色参数，请明确指定是哪种元素的颜色：
1. 为屋顶颜色，使用 "element": "roof"
2. 为墙体颜色，使用 "element": "wall"
3. 为窗户颜色，使用 "element": "window"
4. 为门颜色，使用 "element": "door"
5. 为楼层/地板颜色，使用 "element": "floor"

建议避免推荐已经调整过的相同元素的颜色。`;
      
      // 发送到 GPT
      const result = await sendToGPT(prompt, currentBuildingData);
      
      if (result.success) {
        // 检查是否是参数推荐格式
        if (result.isParameterRecommendation) {
          // 规范化参数名称
          const normalizedData = normalizeParameterData(result.data);
          
          // 确保颜色参数有元素类型
          if ((normalizedData.key.includes('color') || normalizedData.key.includes('material')) && !normalizedData.element) {
            console.warn('颜色参数缺少元素类型，添加默认值');
            normalizedData.element = 'general';
          }
          
          // 设置下一个参数
          set((state) => ({ 
            nextParameter: normalizedData,
            parameterAdjustmentComplete: false,
            currentParameterIndex: state.currentParameterIndex
          }));
          
          // 只有在非首次调用时才添加参数推荐消息到对话记录
          if (!isFirstCall) {
            set((state) => ({
              messages: [...state.messages, { role: 'assistant', content: result.message }],
            }));
          }
          
          return;
        }
        console.error('返回的数据不是参数推荐格式');
      } else {
        console.error('Failed to get next parameter:', result.error);
      }
    } catch (error) {
      console.error('Error setting next parameter:', error);
    }
  },
  
  // 完成当前参数调整
  completeParameterAdjustment: (parameter, element) => {
    // 规范化参数标识符
    const normalizedParameter = normalizeParameterName(parameter);
    
    // 创建带有元素类型的完整参数标识符
    const fullParameter = element ? `${normalizedParameter}.${element}` : normalizedParameter;
    
    set((state) => ({
      currentParameterIndex: state.currentParameterIndex + 1,
      parameterAdjustmentComplete: true,
      parameterHistory: [...state.parameterHistory, fullParameter]
    }));
  },
  
  // 重置参数调整状态
  resetParameterAdjustment: () => {
    set({
      currentParameterIndex: 0,
      parameterAdjustmentComplete: false,
      nextParameter: null,
      parameterHistory: []
    });
  },

  // 处理参数变化并发送到 GPT
  handleParameterChange: async (param, value, userMessage) => {
    // 添加到消息列表
    set((state) => ({
      messages: [...state.messages, { role: 'user', content: userMessage }],
      isLoadingChat: true,
    }));
    
    try {
      // 获取当前建筑数据
      const currentBuildingData = get().buildingData;
      
      // 发送到 GPT
      const result = await sendToGPT(userMessage, currentBuildingData);
      
      if (result.success) {
        // 检查是否是参数推荐数据
        if (result.isParameterRecommendation) {
          // 如果是参数推荐数据，直接更新状态
          set((state) => ({
            messages: [...state.messages, { role: 'assistant', content: result.message }],
            isLoadingChat: false,
            error: null,
            parameterAdjustmentComplete: true
          }));
          
          // 获取下一个参数
          await get().setNextParameter();
          return;
        }
        
        // 验证并更新建筑数据
        const validationResult = validateBuildingData(result.data);
        if (validationResult.valid) {
          // 更新状态并记录响应消息
          await new Promise(resolve => {
            set((state) => ({
              buildingData: result.data,
              messages: [...state.messages, { role: 'assistant', content: result.message }],
              isLoadingChat: false,
              error: null,
              parameterAdjustmentComplete: true
            }));
            // 使用 setTimeout 确保状态更新完成
            setTimeout(resolve, 50);
          });
          
          // 获取下一个参数
          await get().setNextParameter();
        } else {
          // 数据验证失败
          set((state) => ({
            messages: [...state.messages, { role: 'assistant', content: `错误：${validationResult.error}` }],
            isLoadingChat: false,
            error: validationResult.error
          }));
        }
      } else {
        // GPT 处理失败
        set((state) => ({
          messages: [...state.messages, { role: 'assistant', content: `错误：${result.error}` }],
          isLoadingChat: false,
          error: result.error
        }));
      }
    } catch (error) {
      console.error('Error processing parameter change:', error);
      set((state) => ({
        messages: [...state.messages, { role: 'assistant', content: '抱歉，处理您的请求时遇到了错误。' }],
        isLoadingChat: false,
        error: error.message
      }));
    }
  },
}));

export default useStore; 