import { getOpenAIInstance } from './openaiInstance';

// 处理复杂参数数据的函数
const processParameterData = (paramData) => {
  if (!paramData || typeof paramData !== 'object') {
    return paramData;
  }
  
  // 创建新对象，避免修改原始对象
  const processedData = { ...paramData };
  
  // 处理复杂参数对象
  if (processedData.value && typeof processedData.value === 'object') {
    console.log('检测到复杂参数值对象:', processedData.value);
    const key = processedData.key;
    const value = processedData.value;
    
    // 根据参数类型选择要使用的值
    if (key.includes('roof')) {
      if (value.height !== undefined) {
        // 对于屋顶参数，使用高度作为主要参数
        processedData.key = 'roofHeight';
        processedData.label = processedData.label || '屋顶高度';
        processedData.min = value.height - 1000;
        processedData.max = value.height + 1000;
        processedData.value = value.height;
        processedData.step = 100;
        processedData.description = processedData.description || '调整屋顶的高度，影响建筑整体轮廓。';
      } else if (value.pitch !== undefined) {
        // 使用坡度作为主要参数
        processedData.key = 'roofPitch';
        processedData.label = processedData.label || '屋顶坡度';
        processedData.min = Math.max(5, value.pitch - 20);
        processedData.max = Math.min(60, value.pitch + 20);
        processedData.value = value.pitch;
        processedData.step = 5;
        processedData.description = processedData.description || '调整屋顶的坡度，影响排水和美观。';
      }
    } else if (key.includes('window')) {
      if (value.width !== undefined) {
        // 对于窗户参数，使用宽度作为主要参数
        processedData.key = 'windowWidth';
        processedData.label = processedData.label || '窗户宽度';
        processedData.min = Math.max(500, value.width - 500);
        processedData.max = value.width + 500;
        processedData.value = value.width;
        processedData.step = 50;
        processedData.description = processedData.description || '调整窗户的宽度，影响采光和美观。';
      } else if (value.height !== undefined) {
        // 使用高度作为主要参数
        processedData.key = 'windowHeight';
        processedData.label = processedData.label || '窗户高度';
        processedData.min = Math.max(500, value.height - 500);
        processedData.max = value.height + 500;
        processedData.value = value.height;
        processedData.step = 50;
        processedData.description = processedData.description || '调整窗户的高度，影响采光和视野。';
      }
    } else if (key.includes('wall') && value.thickness !== undefined) {
      // 对于墙体参数，使用厚度作为主要参数
      processedData.key = 'wallThickness';
      processedData.label = processedData.label || '墙体厚度';
      processedData.min = Math.max(100, value.thickness - 100);
      processedData.max = value.thickness + 200;
      processedData.value = value.thickness;
      processedData.step = 10;
      processedData.description = processedData.description || '调整墙体的厚度，影响保温和结构强度。';
    } else if (key.includes('door')) {
      if (value.width !== undefined) {
        // 对于门参数，使用宽度作为主要参数
        processedData.key = 'doorWidth';
        processedData.label = processedData.label || '门宽度';
        processedData.min = Math.max(600, value.width - 300);
        processedData.max = value.width + 300;
        processedData.value = value.width;
        processedData.step = 50;
        processedData.description = processedData.description || '调整门的宽度，影响通行和美观。';
      } else if (value.height !== undefined) {
        // 使用高度作为主要参数
        processedData.key = 'doorHeight';
        processedData.label = processedData.label || '门高度';
        processedData.min = Math.max(1800, value.height - 300);
        processedData.max = value.height + 300;
        processedData.value = value.height;
        processedData.step = 50;
        processedData.description = processedData.description || '调整门的高度，影响通行和美观。';
      }
    } else {
      // 对于其他复杂参数，尝试找到第一个数值属性
      const numericProps = Object.entries(value).filter(([_, v]) => typeof v === 'number');
      if (numericProps.length > 0) {
        const [propName, propValue] = numericProps[0];
        processedData.key = `${key}${propName.charAt(0).toUpperCase() + propName.slice(1)}`;
        processedData.label = processedData.label || `${key} ${propName}`;
        processedData.min = Math.max(0, propValue - propValue * 0.3);
        processedData.max = propValue + propValue * 0.3;
        processedData.value = propValue;
        processedData.step = Math.max(1, Math.floor(propValue / 20));
        processedData.description = processedData.description || `调整${key}的${propName}参数。`;
      }
    }
  }
  
  return processedData;
};

// 封装参数为友好的自然语言
const formatParameterAsMessage = (paramData) => {
  try {
    // 根据不同类型的参数创建不同的友好消息
    let friendlyMessage = '';
    
    // 检查参数是否与颜色相关
    const isColorParam = paramData.key.toLowerCase().includes('color') || 
                        paramData.key.toLowerCase().includes('material');
    
    // 检查参数类型
    const paramType = paramData.type || 'slider';
    
    if (isColorParam) {
      // 颜色参数的友好消息
      const element = paramData.element || '建筑元素';
      friendlyMessage = `接下来，我们可以调整${paramData.label}。颜色选择会影响建筑的视觉效果和情感体验。${element === 'roof' ? '屋顶' : element === 'wall' ? '墙体' : element === 'window' ? '窗户' : element === 'door' ? '门' : '建筑元素'}颜色应与整体环境协调，考虑光照反射率和热吸收特性。${paramData.description || ''}`;
    } else if (paramType === 'select' && paramData.options) {
      // 选择类型参数的友好消息
      const optionsText = paramData.options.map(opt => opt.label).join('、');
      friendlyMessage = `接下来，我们可以选择${paramData.label}。可选项包括：${optionsText}。${paramData.description || ''}`;
    } else {
      // 其他数值类型参数的友好消息
      switch(paramData.key) {
        case 'height':
          friendlyMessage = `接下来，我们可以调整 ceiling 高度。当前高度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。建筑高度影响整体规模感和空间利用效率。对于住宅建筑，通常3000-3500毫米的层高能提供舒适的生活空间，同时保持良好的保温性能。${paramData.description || ''}`;
          break;
        case 'height_floor':
          friendlyMessage = `现在，我们可以调整楼层的具体高度。当前楼层高度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间进行调整。楼层高度直接影响到房间的空间感和舒适度。适当的楼层高度可以提升居住体验，并有助于优化采光和通风。${paramData.description || ''}`;
          break;
        case 'roofHeight':
          friendlyMessage = `让我们调整屋顶高度。当前高度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。屋顶高度调整可以改变建筑的轮廓和空间感。较高的屋顶能提供更好的排水和保温性能，也能增加阁楼空间。建议根据整体建筑高度选择合适的屋顶高度，通常为总高度的1/4到1/3。${paramData.description || ''}`;
          break;
        case 'roofPitch':
          friendlyMessage = `接下来可以调整屋顶坡度。当前角度是 ${paramData.value} 度，您可以在 ${paramData.min} 到 ${paramData.max} 度之间调整。屋顶坡度决定了屋顶的排水效果和风格特点。较陡的坡度(30-45度)适合雨雪较多的地区，能有效排水防积雪；平缓的坡度(15-25度)则更适合干燥气候，并能带来现代简约感。${paramData.description || ''}`;
          break;
        case 'pitch_roof':
          friendlyMessage = `接下来可以调整屋顶坡度。当前角度是 ${paramData.value} 度，您可以在 ${paramData.min} 到 ${paramData.max} 度之间调整。屋顶坡度决定了屋顶的排水效果和风格特点。较陡的坡度(30-45度)适合雨雪较多的地区，能有效排水防积雪；平缓的坡度(15-25度)则更适合干燥气候，并能带来现代简约感。${paramData.description || ''}`;
          break;
        case 'roofOverhang':
          friendlyMessage = `接下来可以调整屋顶悬挑。当前悬挑长度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。屋顶悬挑是指屋顶超出墙体边界的水平距离，可以有效保护墙体不受雨水侵蚀，并为窗户提供遮阳。在热带和亚热带地区，较大的悬挑(600-900mm)更为常见，而温带地区则适合中等悬挑(300-600mm)。${paramData.description || ''}`;
          break;
        case 'overhang_roof':
          friendlyMessage = `现在，让我们调整屋顶悬挑。当前悬挑是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。屋顶悬挑是指屋顶超出墙体边界的水平距离，可以有效保护墙体不受雨水侵蚀，并为窗户提供遮阳。在热带和亚热带地区，较大的悬挑(600-900mm)更为常见，而温带地区则适合中等悬挑(300-600mm)。${paramData.description || ''}`;
        case 'wallThickness':
          friendlyMessage = `现在我们来调整墙体厚度。当前厚度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。墙体厚度影响建筑的保温性能和结构稳定性。一般外墙厚度在200-300毫米较为合理，能提供良好的保温隔热效果；内墙可选择100-150毫米，满足承重和隔音需求。${paramData.description || ''}`;
          break;
        case 'floorCount':
          friendlyMessage = `接下来我们可以调整楼层数量。当前楼层数是 ${paramData.value}，您可以设置 ${paramData.min} 到 ${paramData.max} 层不等。楼层数直接决定了建筑的垂直空间分布和总使用面积。单层建筑更适合老年人和行动不便者，而多层建筑则可以在有限的占地面积上提供更多使用空间。考虑到消防安全和结构安全，多层住宅通常不超过4层，而商业建筑则可以更高。${paramData.description || ''}`;
          break;
        case 'totalArea':
          friendlyMessage = `让我们调整建筑的总面积。当前面积是 ${paramData.value} 平方米，您可以在 ${paramData.min} 到 ${paramData.max} 平方米之间调整。总面积是规划建筑功能分区的基础参数。现代舒适的单人住宅通常需要60-80平方米，家庭住宅则需要100-150平方米以上。增加面积可以提供更宽敞的生活空间，但也会增加建造和维护成本。建议根据实际需求和经济条件选择合适的建筑面积。${paramData.description || ''}`;
          break;
        case 'windowSize':
          friendlyMessage = `让我们调整窗户的整体尺寸。当前尺寸是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。窗户的大小影响自然光和通风的进入量。较大的窗户可以增强视野和亮度，但可能会影响保温性能；较小的窗户有助于保温，但可能限制光线和开阔感。选择窗户尺寸时请考虑房间的用途。${paramData.description || ''}`;
          break;
        case 'windowWidth':
          friendlyMessage = `让我们调整窗户的宽度。当前宽度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。窗户的宽度在决定房间的采光和通风方面起着重要作用。较宽的窗户可以提供更多的光线和更好的视野，但可能降低能效。较窄的窗户有助于保温，但可能限制自然光的进入。根据房间的功能和期望的氛围选择宽度。${paramData.description || ''}`;
          break;
        case 'windowHeight':
          friendlyMessage = `让我们调整窗户的高度。当前高度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。窗户的高度影响光线和空气在房间内的流通。较高的窗户可以提供更多的光线和更宏伟的视野，但也可能导致更高的能源成本。较矮的窗户可以增强隐私和保温。选择窗户高度时请考虑房间的需求。${paramData.description || ''}`;
          break;
        case 'doorSize':
          friendlyMessage = `让我们调整门的整体尺寸。当前尺寸是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。门的尺寸影响通行便利性和空间感。较大的门可以促进流动并创造开放感，而较小的门可以提供更亲密的感觉。根据房间的功能和期望的通行水平选择门的尺寸。${paramData.description || ''}`;
          break;
        case 'doorWidth':
          friendlyMessage = `让我们调整门的宽度。当前宽度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。门的宽度对于确保通行便利性和流动性至关重要。较宽的门可以容纳较大的物品并提高通行便利性，而较窄的门可以节省空间。选择门的宽度时请考虑房间的用途和通行需求。${paramData.description || ''}`;
          break;
        case 'doorHeight':
          friendlyMessage = `接下来我们来调整门的尺寸。当前高度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。门的尺寸关系到通行便利性和空间感。标准室内门高度通常为2000-2100毫米，宽度为800-900毫米；入户门可以更宽一些，通常为900-1000毫米，以便于大型物品进出。公共空间的门宽度建议不小于900毫米，以满足轮椅通行需求。${paramData.description || ''}`;
          break;
        case 'wallHeight':
          friendlyMessage = `接下来我们来调整墙体高度。当前高度是 ${paramData.value} 毫米，您可以在 ${paramData.min} 到 ${paramData.max} 毫米之间调整。墙体高度影响建筑的整体视觉效果和内部空间感。标准层高通常在2700-3000毫米，较高的墙体带来更宽敞的感觉，但可能增加建造和维护成本。${paramData.description || ''}`;
          break;
        default: 
          const unit = paramData.unit ? `${paramData.unit}` : '';
          friendlyMessage = `接下来我们可以调整${paramData.label || paramData.key}。当前值是 ${paramData.value}${unit}，您可以在 ${paramData.min} 到 ${paramData.max}${unit} 之间调整。这个参数会影响建筑的整体外观和功能性，尝试不同的值来找到最适合您需求的设置。${paramData.description || ''}`;
      }
    }
    
    return friendlyMessage;
  } catch (error) {
    console.error('格式化参数消息出错:', error);
    return '让我们调整下一个参数。请使用控件调整参数值。';
  }
};

// 发送请求到 OpenAI API
export const sendToGPT = async (userMessage, buildingData, systemPromptText) => {
  try {
    // 构建系统提示
    const finalSystemPrompt = systemPromptText || `You are a building design assistant. Your task is to help modify building designs based on user requests.
You must ALWAYS respond with valid JSON data that follows this exact structure:
{
  "building": {
    "name": string,
    "floors": [
      {
        "name": string,
        "level": number,
        "height": number,
        "material": {
          "color": string (hex color),
          "opacity": number
        },
        "rooms": [
          {
            "name": string,
            "footprint": [[number, number], [number, number], [number, number], [number, number]],
            "walls": [
              {
                "start": [number, number],
                "end": [number, number],
                "thickness": number,
                "material": {
                  "color": string (hex color),
                  "opacity": number
                },
                "window": {
                  "position": number,
                  "verticalPosition": number,
                  "width": number,
                  "height": number,
                  "depth": number,
                  "material": {
                    "color": string (hex color),
                    "opacity": number
                  }
                },
                "door": {
                  "position": number,
                  "width": number,
                  "height": number,
                  "depth": number,
                  "material": {
                    "color": string (hex color),
                    "opacity": number
                  }
                }
              }
            ]
          }
        ],
        "roof": {
          "type": string,
          "height": number,
          "overhang": number,
          "pitch": number,
          "thickness": number,
          "material": {
            "color": string (hex color),
            "opacity": number
          }
        }
      }
    ]
  }
}

Important rules:
1. When adding new floors (increasing level), you MUST include complete room structures with walls, windows, and doors.
2. Each new floor should have at least one room with the same basic structure as the ground floor.
3. Maintain consistent dimensions and materials across floors unless specifically requested to change.
4. All measurements should be in millimeters.
5. Colors must be in hex format (e.g., "#ffffff").
6. Never include comments or explanations in the JSON.
7. Always maintain the exact structure shown above.
8. When modifying existing floors, preserve their unique features while updating requested changes.
9. Ensure all floors have proper connections (stairs, etc.) if needed.
10. The roof property should be at the floor level, not the room level.
11. When changing the height of a floor, you MUST adjust the position of all floors above it to maintain proper vertical alignment.
12. The "roof" data MUST be in the JSON data and ONLY located at the same height as the highest floor.
13. If the JSON data has multiple floors, a "staircase" property must be included in the JSON data.
14. The floor of the upper floor should attach on the top of the floor of the lower floor.
15. If the building has more than one floor, the rooms called "staircase" (on each floor and align with each other) should be included in the JSON data.
16. Each floor should be the same height.

When asked to recommend the next parameter to adjust, respond with a JSON object in this format:
{
  "key": string (parameter identifier),
  "label": string (human-readable label),
  "min": number (minimum value),
  "max": number (maximum value),
  "value": number (current value),
  "step": number (optional, step size for the slider),
  "description": string (provide a DETAILED and COMPREHENSIVE explanation of this parameter, around 2-3 sentences long, including typical values, impact on building design, and professional recommendations),
  "element": string (optional, specifies the element type such as "roof", "wall", "window", "door", "floor"),
  "elementIndex": number (optional, the index of the element if applicable)
}

Parameter recommendation rules:
1. Consider the current building state when recommending parameters
2. Prioritize parameters that would have the most impact on the current design
3. Ensure parameter ranges are reasonable and consistent
4. Provide DETAILED DESCRIPTIONS that explain the parameter's purpose, impact, and typical values in professional terms
5. Use natural, conversational language in descriptions as if you're a professional architect explaining to a client
6. Consider the order of parameters (e.g., basic structure before aesthetics)
7. Avoid recommending parameters that have already been adjusted
8. Make sure the parameter is relevant to the current building state
9. For color parameters, ALWAYS specify the "element" field to indicate which element's color to change
10. Use specific element identifiers like "roof", "wall", "window", "door", "floor" for color parameters
11. Start from the total floor area, total number of floors, total number of rooms, total length of the building, total width of the building, etc.`;

    // 构建用户提示
    const finalUserPrompt = `当前建筑模型数据：
${JSON.stringify(buildingData, null, 2)}

用户请求：${userMessage}

请根据用户请求修改建筑模型数据，并返回完整的 JSON 数据。`;

    // 打印请求内容
    console.log('===== backend > GPT =====');
    console.log('系统提示:', finalSystemPrompt);
    console.log('用户提示:', finalUserPrompt);
    console.log('建筑数据:', JSON.stringify(buildingData, null, 2));
    console.log('============================');

    // Get the latest OpenAI instance with the current API key
    const openai = getOpenAIInstance();
    console.log('Using OpenAI API key:', openai.apiKey.substring(0, 3) + '...');

    // 发送请求到 OpenAI API
    const response = await openai.chat.completions.create({
      model: "gpt-3.5-turbo",
      messages: [
        { role: "system", content: finalSystemPrompt },
        { role: "user", content: finalUserPrompt }
      ],
      temperature: 0.7
    });

    // 提取响应内容
    const content = response.choices[0].message.content;
    
    // 打印响应内容
    console.log('===== GPT > backend =====');
    console.log('原始响应:', content);
    console.log('============================');
    
    // 尝试解析 JSON
    try {
      // 预处理：移除 JSON 中的注释和额外空白
      const cleanContent = content
        .replace(/\/\/.*$/gm, '') // 移除单行注释
        .replace(/\/\*[\s\S]*?\*\//g, '') // 移除多行注释
        .replace(/```json|```/g, ''); // 移除代码块标记
      
      console.log('清理后的内容:', cleanContent);
      
      // 首先尝试直接解析整个响应
      try {
        const jsonData = JSON.parse(cleanContent);
        console.log('成功解析 JSON 数据:', jsonData);
        
        // 检查是否是参数推荐格式
        if (jsonData.key && jsonData.label && 
            (jsonData.min !== undefined) && 
            (jsonData.max !== undefined) && 
            (jsonData.value !== undefined)) {
          console.log('识别为参数推荐格式');
          
          // 处理复杂参数结构
          const processedData = processParameterData(jsonData);
          
          return {
            success: true,
            data: processedData,
            message: formatParameterAsMessage(processedData),
            isParameterRecommendation: true
          };
        }
        
        // 检查是否是建筑数据格式 (具有 building 属性)
        if (jsonData.building) {
          console.log('识别为建筑数据格式');
        return {
          success: true,
          data: jsonData,
            message: "成功更新建筑模型",
            isParameterRecommendation: false
          };
        }
        
        // 如果不符合预期格式，尝试查找嵌套的 JSON 对象
        if (typeof jsonData === 'object') {
          // 检查是否在嵌套结构中有 building 或参数推荐
          for (const key in jsonData) {
            const value = jsonData[key];
            if (typeof value === 'object' && value !== null) {
              if (value.building) {
                console.log('在嵌套结构中找到建筑数据');
                return {
                  success: true,
                  data: value,
                  message: "成功更新建筑模型",
                  isParameterRecommendation: false
                };
              }
              
              if (value.key && value.label && 
                  (value.min !== undefined) && 
                  (value.max !== undefined) && 
                  (value.value !== undefined)) {
                console.log('在嵌套结构中找到参数推荐');
                const processedData = processParameterData(value);
                return {
                  success: true,
                  data: processedData,
                  message: formatParameterAsMessage(processedData),
                  isParameterRecommendation: true
                };
              }
            }
          }
        }
        
        // 无法识别的 JSON 结构
        console.warn('已解析 JSON 但不符合预期格式:', jsonData);
      } catch (directParseError) {
        console.error('直接解析 JSON 时出错:', directParseError);
      }
      
      // 如果直接解析失败，尝试从响应中提取 JSON
      const extractJsonObject = (text) => {
        // 寻找最大的 JSON 对象
        const bracketPairs = [];
        let openBracketIndex = -1;
        let maxLength = 0;
        let maxJsonStart = -1;
        let maxJsonEnd = -1;
        let bracketCount = 0;
        
        // 查找所有可能的 JSON 对象
        for (let i = 0; i < text.length; i++) {
          if (text[i] === '{') {
            if (bracketCount === 0) {
              openBracketIndex = i;
            }
            bracketCount++;
          } else if (text[i] === '}') {
            bracketCount--;
            if (bracketCount === 0 && openBracketIndex !== -1) {
              const length = i - openBracketIndex + 1;
              if (length > maxLength) {
                maxLength = length;
                maxJsonStart = openBracketIndex;
                maxJsonEnd = i + 1;
              }
              bracketPairs.push([openBracketIndex, i]);
              openBracketIndex = -1;
            }
          }
        }
        
        // 如果找到完整的 JSON 对象，尝试解析
        if (maxJsonStart !== -1 && maxJsonEnd !== -1) {
          const jsonCandidate = text.substring(maxJsonStart, maxJsonEnd);
          try {
            return JSON.parse(jsonCandidate);
          } catch (e) {
            console.warn('提取的候选 JSON 解析失败:', e);
            // 对于所有可能的 JSON 片段，尝试修复和解析
            for (const [start, end] of bracketPairs) {
              try {
                const fragment = text.substring(start, end + 1);
                // 尝试修复常见问题
                const fixed = fixCommonJsonErrors(fragment);
                return JSON.parse(fixed);
              } catch (err) {
                // 继续尝试下一个
              }
            }
          }
        }
        
        return null;
      };
      
      // 修复常见的 JSON 错误
      const fixCommonJsonErrors = (jsonStr) => {
        let fixed = jsonStr;
        
        // 修复缺少逗号的问题
        fixed = fixed.replace(/(["\d])\s*}\s*"/g, '$1,"');
        fixed = fixed.replace(/(["\d])\s*}\s*{/g, '$1},{');
        
        // 修复缺少引号的键
        fixed = fixed.replace(/([{,]\s*)([a-zA-Z_][a-zA-Z0-9_]*)\s*:/g, '$1"$2":');
        
        // 修复尾部逗号
        fixed = fixed.replace(/,\s*}/g, '}');
        fixed = fixed.replace(/,\s*]/g, ']');
        
        // 修复错误的布尔值或null值
        fixed = fixed.replace(/"(true|false|null)"/g, '$1');
        
        return fixed;
      };
      
      // 提取并尝试解析 JSON
      const extractedJson = extractJsonObject(cleanContent);
      if (extractedJson) {
        console.log('从响应中提取到 JSON:', extractedJson);
        
        // 检查是否是参数推荐格式
        if (extractedJson.key && extractedJson.label && 
            (extractedJson.min !== undefined) && 
            (extractedJson.max !== undefined) && 
            (extractedJson.value !== undefined)) {
          const processedData = processParameterData(extractedJson);
          return {
            success: true,
            data: processedData,
            message: formatParameterAsMessage(processedData),
            isParameterRecommendation: true
          };
        }
        
        // 检查是否是建筑数据格式
        if (extractedJson.building) {
          return {
            success: true,
            data: extractedJson,
            message: "成功更新建筑模型",
            isParameterRecommendation: false
          };
        }
      }
      
      // 如果无法提取完整的 JSON，尝试使用正则表达式查找参数推荐
      const paramPattern = /"key"\s*:\s*"([^"]+)"\s*,\s*"label"\s*:\s*"([^"]+)"\s*,\s*"min"\s*:\s*(\d+)\s*,\s*"max"\s*:\s*(\d+)\s*,\s*"value"\s*:\s*(\d+)/;
      const paramMatch = cleanContent.match(paramPattern);
      
      if (paramMatch) {
        console.log('通过正则表达式找到参数推荐:', paramMatch);
        try {
          // 构建参数对象
          const paramData = {
            key: paramMatch[1],
            label: paramMatch[2],
            min: parseInt(paramMatch[3]),
            max: parseInt(paramMatch[4]),
            value: parseInt(paramMatch[5]),
            step: 1,
            description: "调整这个参数以修改建筑设计。"
          };
          
          const processedData = processParameterData(paramData);
          return {
            success: true,
            data: processedData,
            message: formatParameterAsMessage(processedData),
            isParameterRecommendation: true
          };
        } catch (paramError) {
          console.error('解析参数推荐数据时出错:', paramError);
        }
      }
      
      // 如果所有方法都失败，返回一个默认的参数推荐
      console.warn('无法解析有效 JSON，返回默认参数');
      const defaultParam = {
        key: "height",
        label: "楼层高度",
        min: 2500,
        max: 4000,
        value: 3000,
        step: 100,
        description: "调整楼层的高度。"
      };
      
      return {
        success: true,
        data: defaultParam,
        message: formatParameterAsMessage(defaultParam),
        isParameterRecommendation: true
      };
    } catch (error) {
      console.error('处理 GPT 响应时出错:', error);
      return {
        success: false,
        error: '处理 GPT 响应时出错: ' + error.message
      };
    }
  } catch (error) {
    console.error('发送请求到 GPT 时出错:', error);
    return {
      success: false,
      error: '发送请求到 GPT 时出错'
    };
  }
};

// 验证建筑数据结构的函数
export const validateBuildingData = (data) => {
  console.log('开始验证建筑数据...');
  console.log('输入数据:', data);
  
  if (!data || typeof data !== 'object') {
    console.error('数据不是有效的对象');
    return { valid: false, error: '数据不是有效的对象' };
  }
  
  if (!data.building) {
    console.error('数据缺少 building 属性');
    return { valid: false, error: '数据缺少 building 属性' };
  }
  
  const building = data.building;
  console.log('建筑数据:', building);
  
  // 检查基本属性
  if (!building.name || typeof building.name !== 'string') {
    console.error('建筑名称无效');
    return { valid: false, error: '建筑名称无效' };
  }
  
  if (!Array.isArray(building.floors) || building.floors.length === 0) {
    console.error('楼层数据无效');
    return { valid: false, error: '楼层数据无效' };
  }

  // 检查每个楼层
  for (let i = 0; i < building.floors.length; i++) {
    const floor = building.floors[i];
    console.log(`检查第 ${i + 1} 层:`, floor);
    
    if (!floor.name || typeof floor.name !== 'string') {
      console.error(`第 ${i + 1} 层名称无效`);
      return { valid: false, error: `第 ${i + 1} 层名称无效` };
    }
    
    if (!Array.isArray(floor.rooms) || floor.rooms.length === 0) {
      console.error(`第 ${i + 1} 层房间数据无效`);
      return { valid: false, error: `第 ${i + 1} 层房间数据无效` };
    }

    // 检查每个房间
    for (let j = 0; j < floor.rooms.length; j++) {
      const room = floor.rooms[j];
      console.log(`检查第 ${i + 1} 层第 ${j + 1} 个房间:`, room);
      
      if (!room.name || typeof room.name !== 'string') {
        console.error(`第 ${i + 1} 层第 ${j + 1} 个房间名称无效`);
        return { valid: false, error: `第 ${i + 1} 层第 ${j + 1} 个房间名称无效` };
      }
      
      // 检查 footprint 属性
      if (!room.footprint || !Array.isArray(room.footprint) || room.footprint.length < 4) {
        console.error(`第 ${i + 1} 层第 ${j + 1} 个房间 footprint 无效`);
        return { valid: false, error: `第 ${i + 1} 层第 ${j + 1} 个房间 footprint 无效` };
      }
      
      // 检查 walls 属性
      if (!room.walls || !Array.isArray(room.walls) || room.walls.length === 0) {
        console.error(`第 ${i + 1} 层第 ${j + 1} 个房间 walls 无效`);
        return { valid: false, error: `第 ${i + 1} 层第 ${j + 1} 个房间 walls 无效` };
      }
      
      // 检查每个墙体
      for (let k = 0; k < room.walls.length; k++) {
        const wall = room.walls[k];
        if (!wall.start || !Array.isArray(wall.start) || wall.start.length !== 2) {
          console.error(`第 ${i + 1} 层第 ${j + 1} 个房间第 ${k + 1} 个墙体起点无效`);
          return { valid: false, error: `第 ${i + 1} 层第 ${j + 1} 个房间第 ${k + 1} 个墙体起点无效` };
        }
        
        if (!wall.end || !Array.isArray(wall.end) || wall.end.length !== 2) {
          console.error(`第 ${i + 1} 层第 ${j + 1} 个房间第 ${k + 1} 个墙体终点无效`);
          return { valid: false, error: `第 ${i + 1} 层第 ${j + 1} 个房间第 ${k + 1} 个墙体终点无效` };
        }
        
        if (!wall.thickness || typeof wall.thickness !== 'number') {
          console.error(`第 ${i + 1} 层第 ${j + 1} 个房间第 ${k + 1} 个墙体厚度无效`);
          return { valid: false, error: `第 ${i + 1} 层第 ${j + 1} 个房间第 ${k + 1} 个墙体厚度无效` };
        }
        
        if (!wall.material || typeof wall.material !== 'object') {
          console.error(`第 ${i + 1} 层第 ${j + 1} 个房间第 ${k + 1} 个墙体材料无效`);
          return { valid: false, error: `第 ${i + 1} 层第 ${j + 1} 个房间第 ${k + 1} 个墙体材料无效` };
        }
      }
    }
  }
  
  console.log('建筑数据验证通过');
  return { valid: true };
}; 